<template>
  <div>
    <h2 class="headline font-weight-light">
      {{ project.title }}
    </h2>
    <div class="mt-4">
    {{ project.subtitle }}
    </div>
    <div class="mt-4">
      <v-carousel
          v-if="project.imgs"
          :continuous="true"
          :cycle="true"
          :show-arrows="true"
          hide-delimiter-background
          delimiter-icon="mdi-minus"
          height="auto"
          style="min-height: 400px;"
      >
        <v-carousel-item
            v-for="(img, i) in project.imgs"
            :key="i"
        >
          <div class="d-flex justify-center">
              <img height="auto" width="auto" style="max-width: 100%; max-height: 500px;" :src="img" />
          </div>
        </v-carousel-item>
      </v-carousel>
      <div v-if="project.info" class="mt-2">
        {{ project.info }}
      </div>
      <div v-if="project.link">
        <a :href="project.link">{{project.linkText}}</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
name: "SingleProject",
  props: {
    project: {
      required: false,
    },
    title: {
      required: false,
      default: ""
    },
    subtitle: {
      required: false,
      default: ""
    },
    imgs: {
      required: false,
      default: null
    }
  }
}
</script>

